	import {
		myRequestGet
	} from '@/utils/request.js';
	import uniPopup from '..//uni-popup/uni-popup.vue';
	import shareBtn from '../share-btn/share-btn.vue';
	
	export default {
		data() {
			return {
				sharedata: '',
				video1: [],
				article1: [],
				video2: [],
				article2: [],
				video3: [],
				article3: []
			};
		},
		created() {
			this.getVideos1();
			this.getArticle1();
			this.getVideos2();
			this.getArticle2();
			this.getVideos3();
			this.getArticle3();
		},
		
		methods: {
           shareOn(){
			   this.$refs.sharepopup.open();
		   },
			
			// 点击进入文章详情页
			goArticleDetail(item) {
				uni.navigateTo({
					url: `/pages/detail03/detail03?id=${item.id}`
				});
			},
			// 点击进入视频详情页
			goVideoDetail(item) {
				uni.navigateTo({
					url: `/pages/detailsp03/detailsp03?id=${item.id}`
				});
			},
			async getVideos1() {
				let result = await myRequestGet('/api/v1/fatiao/index/list', {
					id: 6,
					p: 1,
					order: 1,
					limit: 1,
					channel_id: 6
				});
				console.log(result);
				this.video1 = result;
			},
			async getArticle1() {
				let result = await myRequestGet('/api/v1/fatiao/article/list', {
					id: 6,
					p: 1,
					order: 1,
					limit: 6,
					channel_id: 6
				});
				console.log(result);
				this.article1 = result;
			},
			async getVideos2() {
				let result = await myRequestGet('/api/v1/fatiao/index/list', {
					id: 6,
					p: 2,
					order: 1,
					limit: 1,
					channel_id: 6
				});
				console.log(result);
				this.video2 = result;
			},
			async getArticle2() {
				let result = await myRequestGet('/api/v1/fatiao/article/list', {
					id: 6,
					p: 2,
					order: 1,
					limit: 6,
					channel_id: 6
				});
				console.log(result);
				this.article2 = result;
			},
			async getVideos3() {
				let result = await myRequestGet('/api/v1/fatiao/index/list', {
					id: 6,
					p: 3,
					order: 1,
					limit: 1,
					channel_id: 6
				});
				console.log(result);
				this.video3 = result;
			},
			async getArticle3() {
				let result = await myRequestGet('/api/v1/fatiao/article/list', {
					id: 6,
					p: 3,
					order: 1,
					limit: 6,
					channel_id: 6
				});
				console.log(result);
				this.article3 = result;
			},
			open: function() {
				this.$refs.sharepopup.open();
			}
		},
	
	components: {
		uniPopup,
		shareBtn
	}
	};
